.app-container {
  min-width: 100%;
  min-height: 100%;
  scroll-behavior: smooth;
  scroll-snap-type: mandatory;
  isolation: isolate;
}

.bgVideo {
  position: fixed;
  inset: 0;
  isolation: isolate;
}
video {
  min-height: 100%;
  min-width: 100%;
  padding: 0;
  margin: 0;
  object-fit: cover;
}

.bgVideo::after {
  content: "";
  position: absolute;
  background-color: rgba(5, 5, 5, 0.93);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.sectionWrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 20px;
  z-index: 99;
}

@media only screen and (min-width: 1200px) {
  .sectionWrapper {
    margin: 0 auto;
    width: 80%;
  }
}
