.contactContainer {
  width: 100%;
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.contactRight {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 20px;
}

.contactLeft {
  width: 100%;
  padding: 20px;
}
.contactLeft > form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 100%;
}

.contactLeft > form > .formInformation {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 1rem;
}

.contactLeft > form > label {
  width: 100%;
  text-align: left;
}

.contactLeft > form > .formInformation > input {
  width: 100%;
  padding: 5px;
  border: none;
  border-radius: 5px;
}

.contactLeft > form > .formInformation > textarea {
  height: 100px;
  padding: 5px;
}

.submitBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2px;
  width: 90px;
  height: 40px;
  border-radius: 10px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-shadow: 2px 3px 20px purple;
}
.submitBtn > p,
.submitBtn > p > svg {
  color: rgb(241, 238, 238);
  font-size: 1.2rem;
}
@media only screen and (min-width: 750px) {
  .contactRight {
    width: 500px;
  }
  .contactLeft {
    width: 500px;
    display: flex;
    justify-content: flex-end;
  }
}

@media only screen and (min-width: 1200px) {
  .contactContainer {
    flex-direction: row;
  }

  .contactLeft {
    display: flex;
    justify-content: flex-end;
  }

  .contactLeft > form {
    width: 100%;
  }
}
