* {
  color: white;
  font-weight: 400;
}

.homeContainer {
  width: 100%;
  height: 100dvh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.homeWrapper {
  word-spacing: 2px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.homeWrapper > h1 {
  font-size: 2rem;
  text-align: center;
}

.homeWrapper > h2 {
  text-align: center;
  font-size: 1.2em;
  line-height: 2rem;
}

.name-span {
  font-size: 2rem;
}

.homeWrapper > h2 > span {
  font-size: 2rem;
}

.homeWrapper > .pWrapper {
  width: 375px;
  height: auto;
}

.homeWrapper > .pWrapper > .parDiv > p {
  width: 100%;
  padding: 5px;
  text-align: justify;
  word-spacing: -2px;
  word-wrap: break-word;
  height: 100%;
  font-size: 14px;
  font-weight: 400;
  font-family: sans-serif;
}

.contactBtn {
  width: 150px;
  height: 50px;
  border-radius: 10px;
  color: black;
  border: none;
  box-shadow: -1px 1px 20px purple;
  cursor: pointer;
}

.homeWrapper > ul {
  display: flex;
  gap: 1rem;
  list-style-type: none;
  align-items: center;
  justify-content: start;
  width: 100%;
  height: 40px;
  overflow: hidden;

  padding-left: 5px;
}
.homeWrapper > ul > .listContainer > li {
  padding: 5px;
  border: 2px solid purple;
  border-radius: 10px;
  font-weight: 200;
  font-family: sans-serif;
  font-size: 0.8rem;
}
/* Type writter */
.letterType {
  position: relative;
  isolation: isolate;
}

.blockSpan {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: black;
}

@media only screen and (min-width: 768px) {
  .homeWrapper {
    width: 100%;
  }
  .homeWrapper > h1 {
    font-size: 4rem;
  }

  .homeWrapper > h2 {
    font-size: 2rem;
    line-height: 3rem;
  }

  .homeWrapper > h2 > span {
    font-size: 4rem;
  }

  .homeWrapper > .pWrapper {
    width: 100%;
  }
  .homeWrapper > .pWrapper > .parDiv > p {
    font-size: 1.2rem;
  }
  .homeWrapper > ul {
    height: 70px;
  }

  .homeWrapper > ul > .listContainer > li {
    font-size: 1.2rem;
    height: 100%;
  }
  .contactBtn {
    font-size: 1.5rem;
    width: 200px;
    height: 80px;
  }
}
