.skillcontainer {
  width: 100%;
  height: 600dvh;
  position: relative;
}

.skilltitleicon {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: start;
  position: sticky;
  top: 0;
  height: 50vh;
  width: 100%;
  z-index: 10;
}

.skilltitleicon > p {
  font-size: 2.5rem;
}

.skillprogressbar {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.skillprogressbar > .flipContainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  perspective: 1000px;
}

.skillprogressbar > .flipContainer > .flipInner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  background-color: transparent;
  width: 90%;
  height: 450px;
  padding: 10px;
  border-radius: 10px;
  transform-style: preserve-3d;
  overflow: hidden;
  position: relative;
}

.skillsRateTitle > h1 {
  font-size: 1.2rem;
  text-align: center;
}

.skillsRateTitle {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.skillsRateTitle > p {
  font-size: 0.9rem;
  font-family: sans-serif;
  text-align: justify;
  word-spacing: -1px;
}

.fronPage,
.backFlip {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.backFlip > h1 {
  font-size: 1.2rem;
}

@media only screen and (min-width: 750px) {
  .skillsRateTitle > p {
    font-size: 1.2rem;
    width: 600px;
    margin: 0 auto;
  }

  .skilltitleicon > h1 {
    font-size: 4rem;
  }
}
