.faqConatainer {
  width: 100%;
  height: 100dvh;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 2rem;
}

.faqTitleIcons {
  justify-self: start;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.faqTitleIcons > p {
  padding-top: 10px;
  font-size: 3rem;
}

.faqConatainer > ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  padding: 0px 10px;
}
.faqConatainer > ul > .faqListContainer > li {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.faqAccordion {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.faqAccordion > h2 {
  font-size: 0.8rem;
  border-bottom: 2px solid white;
}

.faqAccordion > p {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: end;
  font-size: 1.2rem;
  cursor: pointer;
}

.faqConatainer > ul > .faqListContainer > li > p {
  text-align: justify;
  word-spacing: -1px;
  font-weight: 400;
  font-family: sans-serif;
  overflow: hidden;
  height: 0;
  padding: 5px;
  font-size: 0.8rem;
}

@media only screen and (min-width: 414px) {
  .faqAccordion > h2 {
    font-size: 1rem;
    border-bottom: 2px solid rgb(241, 237, 237);
  }
}

@media only screen and (min-width: 768px) {
  .faqTitleIcons > p,
  .faqTitleIcons > h1 {
    padding-top: 10px;
    font-size: 4rem;
  }

  .faqAccordion > h2 {
    font-size: 2rem;
    border-bottom: 2px solid rgb(241, 237, 237);
  }
  .faqConatainer {
    gap: 2rem;
    justify-content: center;
  }
  .faqConatainer > ul > .faqListContainer > li > p {
    font-size: 1.2rem;
  }
}
